import React from "react";

import { useTheme } from "@material-ui/core/styles";
import {
  makeStyles,
  Typography,
  Button,
  Grid,
  Card,
  CardContent,
} from "@material-ui/core";
import { Link } from "react-router-dom";

const useStyles = makeStyles({
  btn: {
    padding: 20,
    backgroundColor: "#333",
    color: "#fff",
  },
  title: {
    backgroundColor: "#345BAE",
    padding: "11px 10px",
    color: "#fff",
    borderRadius: 11,
  },
  footer: {
    width: "100%",
    textAlign: "center",
  },
  footerlink: {
    backgroundColor: "#345BAE",
    padding: "11px 10px",
    borderRadius: 11,
    color: "#fff",
  },
  getcol: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-around",
    width: "100%",
  },
  noUpdates: {
    padding: "1em",
    textAlign: "center",
  },
});

export default function VerticleList(props) {
  console.log(props.activityDetails);

  const classes = useStyles();

  return (
    <>
      <div className={classes.title}>
        <Typography variant="h6">Latest Updates</Typography>
      </div>
      {props.activityDetails ? (
        props.activityDetails.slice(0, 3).map((detail, detailIndex) => {
          return (
            <Card
              variant="outlined"
              style={{ margin: "5px", width: "100%" }}
              elevation={3}
            >
              <CardContent>
                <Grid container spacing={2}>
                  <Grid item xs={12} md={12}>
                    <Typography variant="p" className={classes.getcol}>
                      Updated by:{detail.data[detail.data.length - 1].user}
                      {/* {detail.user} */}
                    </Typography>
                    <Typography variant="p" className={classes.getcol}>
                      Update Date :{detail.data[detail.data.length - 1].date}
                    </Typography>

                    <Typography variant="p" className={classes.getcol}>
                      Activity Name:
                      {detail.data[detail.data.length - 1].activityName}
                    </Typography>
                    <Typography variant="p" className={classes.getcol}>
                      Update Percentage:
                      {detail.data[detail.data.length - 1].updatedPercentage}%
                    </Typography>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          );
        })
      ) : (
        <div className={classes.noUpdates}>
          <Typography variant="p">Updates Not Available</Typography>
        </div>
      )}
      <div className={classes.footer}>
        <Link
          style={{ textDecoration: "none" }}
          to={{
            state: {
              activityDetails: props.activityDetails,
            },
            pathname: `/overview/viewupdates`,
          }}
        >
          <Typography variant="p" className={classes.footerlink}>
            View More {">>"}
          </Typography>
        </Link>
      </div>
    </>
  );
}
