import React, { useState } from "react";
import {
  RadioGroup,
  FormControlLabel,
  Radio,
  Card,
  CardContent,
  Typography,
  Button,
  CardActions,
  Grid,
  TextField,
} from "@material-ui/core";
import {
  Edit as EditIcon,
  Add as AddIcon,
  Close as CloseIcon,
  AddComment as AddCommentIcon,
} from "@material-ui/icons";
import BarchartData from "./Barchart";
export default function Activity({
  editActivity,
  activityName,
  estimatedcompletion,
  actualcompletion,
  index,
  addRemark,
  activityRemark,
  isEditable,
  estimatedcompletion_by_cost,
  actualcompletion_by_cost,
}) {
  var [toggleRemark, setToggleRemark] = useState(false);
  var [remark, setRemark] = useState("");
  var [label1, setLabel1] = useState("Estimated Completion");
  var [label2, setLabel2] = useState("Actual Completion");
  const handleChange = e => {
    if (e.target.value == "Cost") {
      console.log(estimatedcompletion_by_cost);
      setValue(estimatedcompletion_by_cost);
      setValue2(actualcompletion_by_cost);
      setLabel1("Estimated Cost");
      setLabel2("Actual Cost");
    } else {
      setValue(estimatedcompletion);
      setValue2(actualcompletion);
      setLabel1("Estimated Completion");
      setLabel2("Actual Completion");
    }
  };
  var [value1, setValue] = useState(estimatedcompletion);
  var [value2, setValue2] = useState(actualcompletion);

  return (
    <Card variant="outlined" style={{ padding: "5px", marginBottom: "15px" }}>
      <CardContent>
        <Typography variant="h5">
          <strong>{activityName}</strong>
        </Typography>
        <RadioGroup
          row
          defaultValue="Time"
          color="secondary"
          onChange={handleChange}
        >
          <FormControlLabel
            value="Time"
            label="Time"
            control={<Radio color="default" />}
          />
          <FormControlLabel
            value="Cost"
            label="Cost"
            control={<Radio color="default" />}
          />
        </RadioGroup>
        <Grid container>
          <Grid item xs={12} sm={12} lg={3} md={3}>
            <strong>{label1}:</strong>
          </Grid>
          <Grid item xs={12} sm={12} lg={9} md={9}>
            {value1 &&
            value1 !== "Not available" &&
            value1 !== "Not started yet" ? (
              label1 === "Estimated Cost" ? (
                parseFloat(value1).toFixed(2) === "NaN" ? (
                  <span style={{ verticalAlign: "sub" }}>
                    <progress value="0" max="100"></progress>0.00%
                  </span>
                ) : (
                  <span style={{ verticalAlign: "sub" }}>
                    <progress
                      value={parseFloat(value1).toFixed(2)}
                      max="100"
                    ></progress>{" "}
                    {parseFloat(value1).toFixed(2)}
                  </span>
                )
              ) : (
                <span style={{ verticalAlign: "sub" }}>
                  <progress
                    value={parseFloat(value1).toFixed(2)}
                    max="100"
                  ></progress>{" "}
                  {parseFloat(value1).toFixed(2)}%
                </span>
              )
            ) : (
              <span style={{ verticalAlign: "sub" }}>
                <progress value="0" max="100"></progress>0.00%
              </span>
            )}
          </Grid>
        </Grid>
        <Grid container>
          <Grid item xs={12} sm={12} lg={3} md={3}>
            <strong>{label2} :</strong>
          </Grid>
          <Grid item xs={12} sm={12} lg={9} md={9}>
            {value2 &&
            value2 !== "Not available" &&
            value2 !== "Not started yet" ? (
              label2 === "Actual Cost" ? (
                parseFloat(value2).toFixed(2) === "NaN" ? (
                  <span style={{ verticalAlign: "sub" }}>
                    <progress value="0" max="100"></progress>0.00%
                  </span>
                ) : (
                  <span style={{ verticalAlign: "sub" }}>
                    <progress
                      value={parseFloat(value2).toFixed(2)}
                      max={value1}
                    ></progress>{" "}
                    {parseFloat(value2).toFixed(2)}
                  </span>
                )
              ) : (
                <span style={{ verticalAlign: "sub" }}>
                  <progress
                    value={parseFloat(value2).toFixed(2)}
                    max="100"
                  ></progress>{" "}
                  {parseFloat(value2).toFixed(2)}%
                </span>
              )
            ) : (
              <span style={{ verticalAlign: "sub" }}>
                <progress value="0" max="100"></progress>0.00%
              </span>
            )}
          </Grid>
        </Grid>
        {/* {activityRemark !== undefined ? (
          <Typography>
            <strong>Remark :</strong> {activityRemark}
          </Typography>
        ) : (
          <></>
        )} */}

        {/* <Button
          variant="contained"
          color="primary"
          style={{ marginTop: "1rem" }}
          onClick={() => editActivity(activityName, index)}
          startIcon={<EditIcon />}
        >
          Edit
        </Button> */}
        {/* {isEditable && (
          <>
            {!toggleRemark ? (
              <Button
                variant="contained"
                color="primary"
                style={{ marginTop: "1rem" }}
                onClick={() => setToggleRemark(true)}
                startIcon={<AddCommentIcon />}
              >
                Remarks
              </Button>
            ) : (
              <>
                <CardActions style={{ marginLeft: "5px" }}>
                  <Grid container>
                    <Grid className="mt-4" item>}
                      <TextField
                        name="remark"
                        value={remark}
                        variant="outlined"
                        required
                        onChange={e => setRemark(e.target.value)}
                        id="remark"
                        label="Remarks"
                        autoFocus
                      />
                    </Grid>
                    <Grid className="ml-2 mt-4" item>
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => {
                          setToggleRemark(false);
                          // addDeliveryDate(deliveryDate, id);
                          addRemark(remark, index);
                        }}
                        startIcon={<AddIcon />}
                      >
                        Done
                      </Button>
                    </Grid>
                    <Grid className="ml-2 mt-4" item>
                      <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => setToggleRemark(false)}
                        startIcon={<CloseIcon />}
                      >
                        Cancel
                      </Button>
                    </Grid>
                  </Grid>
                </CardActions>
              </>
            )}
          </>
        )} */}
      </CardContent>
    </Card>
  );
}
