import React, { useState, useEffect, useMemo } from "react";
import debounce from "lodash/debounce";
import {
  InputLabel,
  TextField,
  Grid,
  Button,
  FormControl,
  Select,
  MenuItem,
  Typography,
} from "@material-ui/core";
import { Save, Cancel, Add as AddIcon } from "@material-ui/icons";
import { Autocomplete } from "@material-ui/lab";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import Style from "./style.module.scss";
import { isInteger } from "lodash";
import axios from "axios";

const AddLabour = props => {
  const [open, setOpen] = useState(false);
  const [labourName, setlabourname] = useState("");
  const [labour, setlabour] = useState([]);
  const [txt, setTxt] = useState("");
  const [toggleText, setToggleText] = useState(false);
  const [activities, setActivities] = useState([]);
  const [activity, setActivity] = useState("");
  const [projectType, setProjectType] = useState(global.config.secureStorage.getItem("projectType"));
  const loading = open && labour.length === 0;
  const [mainActivities, setMainActivities] = useState([]);

  const onInputChange = e => {
    const { value } = e.target;

    const re = /^[A-Za-z]+$/;
    if (value === "" || re.test(value)) {
      setTxt(value);
    }
  };

  useEffect(() => {
    if (!loading) {
      return undefined;
    }
    var tempMaterials = new Set();
    (() => {
      props.storeData.map(ele => {
        return tempMaterials.add(ele.materialName);
      });
      setlabour(Array.from(tempMaterials));
    })();
  }, [loading]);

  useEffect(() => {
    if (!open) {
      setlabour([]);
    }
    getActivities();
  }, [open]);

  async function getActivities() {
    let projectId = await global.config.secureStorage.getItem("projectId");
    const response = await axios.get(
      `${global.config.backendURL}/activities/getDefaultActivities/${projectId}`
    );
    // if(projectType === "Infrastructure"){
    //   const mainActivities = response.data.data.map((mainAct,index)=>{
    //     console.log(mainAct)
    //     return {
    //       id: index,
    //       title : mainAct.name,
    //       activities : mainAct.data
    //     };
    //   });
    //   setMainActivities(mainActivities);
    // }
    // else{
    //   setActivities(response.data.data[0].data);  
    // }
    console.log('asdasdasd',response)
    setActivities(response.data.data);  
  }

  // const handleMainActivityChange = (event) =>{
  //    const value = event.target.value;
  //    const id = mainActivities.findIndex(act=>{
  //      return act.title === value; 
  //    });
  //    console.log(id)
  //    if(id>=0){
  //     const activity = mainActivities[id].activities;
  //     setActivities(activity);  
  //     props.handleMainActivity(event);  
  //    }
  // }

  return (
    <div>
      <section className={Style.create__material}>
        <form onSubmit={props.saveLabour}>
          <center>
            <Typography variant="overline" style={{ fontSize: "1.5em" }}>
              Add to Labour
            </Typography>
          </center>
          <br />

          <br />
          <TextField
            value={props.labourName}
            margin="dense"
            name="labourName"
            id="labourName"
            type="text"
            variant="outlined"
            label="Labour"
            fullWidth
            onKeyPress={event => (event.charCode >= 65 && event.charCode <= 90) || (event.charCode >= 97 && event.charCode <= 122)}
            onChange={event => {
              console.log(Number.isInteger(event.target.value));
            
              
              props.handleChange(event);

            }}
            required
          />
          <br />
          <br />
          {/* {projectType === "Infrastructure"?
          <>
          <FormControl fullWidth>
            <InputLabel id="activity">Main Activity</InputLabel>

            <Select
              labelId="mainActivity"
              id="mainActivity"
              value={props.mainActivity}
              label="mainActivity"
              onChange={event => {
                handleMainActivityChange(event);
              }}
              required
            >
              {mainActivities.map(item => (
                <MenuItem value={item.title}>{item.title}</MenuItem>
              ))}
            </Select>
          </FormControl>
          <br />
          <br />
          </>
          :''
          } */}

          <FormControl fullWidth>
            <InputLabel id="activity">Activity</InputLabel>

            <Select
              labelId="activity"
              id="activity"
              value={props.activity}
              label="activity"
              onChange={event => {
                props.handleActivity(event);
              }}
              required
            >
              {activities.map(item => (
                <MenuItem value={item.title}>{item.title}</MenuItem>
              ))}
            </Select>
          </FormControl>
          <br />
          <br />
          <TextField
            value={props.unit}
            margin="dense"
            name="unit"
            id="unit"
            type="text"
            variant="outlined"
            label="Unit"
            fullWidth
            onKeyPress={event => (event.charCode >= 65 && event.charCode <= 90) || (event.charCode >= 97 && event.charCode <= 122)}
            onChange={e => props.handleChange(e)}
            required
          />
          <br />
          <br />
          <TextField
            name="number_of_labourers"
            id="number_of_labourers"
            variant="outlined"
            onChange={event => {
              props.handleChange(event);
            }}
            label="Number of labourers"
            value={props.number_of_labourers}
            type="number"
            required
            fullWidth
          />
          <br />
          <br />
          <TextField
            name="labourRate"
            id="labourRate"
            variant="outlined"
            onChange={event => {
              props.handleChange(event);
            }}
            label="Labour Salary"
            value={props.labourRate}
            type="number"
            required
            fullWidth
          />
          <br />

          <br />
          <TextField
            name="labourAmount"
            id="labourAmount"
            variant="outlined"
            onChange={event => {
              if (isInteger(parseInt(event.target.value))) {
                props.handleChange(event);
              }
            }}
            label="Amount"
            value={props.labourRate * props.number_of_labourers}
            type="number"
            required
            fullWidth
            disabled
          />
          <Grid justify="center" container>
            <Grid className="mr-4" item>
              <Button
                variant="contained"
                size="large"
                type="submit"
                startIcon={<Save />}
              >
                Save
              </Button>
            </Grid>
            <Grid item>
              <Button
                color="secondary"
                size="large"
                onClick={props.toggleCreateNewLabour}
                startIcon={<Cancel />}
              >
                Cancel
              </Button>
            </Grid>
          </Grid>
        </form>
      </section>
    </div>
  );
};
export default AddLabour;
