import React, { Component } from "react";
import { Button, Grid } from "@material-ui/core";
import EditLabour from "./EditLabour";
import AddLabour from "./AddLabour";
// import { blue } from "@material-ui/core/colors";
import { Add } from "@material-ui/icons";
import Style from "./style.module.scss";
import PropTypes from "prop-types";
import MUIDataTable from "mui-datatables";
import { connect } from "react-redux";
import {
  addMaterial,
  getMaterial,
  updateMaterial,
  deleteMaterial,
  addDeliveryDate,
  getStore,
} from "../../Redux/materialRedux/materialAction";
import Notifications from "react-notification-system-redux";
import axios from "axios";
import SlideTransition from "@material-ui/pickers/views/Calendar/SlideTransition";
// import Typography from "material-ui/styles/typography";
// import { each } from "lodash";

class Display extends Component {
  constructor(props) {
    super(props);
    const role = global.config.secureStorage.getItem("role");
    const editRoles = ["purchase_manager", "project_manager", "super_admin"];
    const isEditable = editRoles.find(item => item == role) ? true : false;
    this.state = {
      labourName: "",
      labourRate: "",
      labourAmount: "",
      unit: "",
      number_of_labourers: "",
      activity: "",
      isCreateNewLabour: false,
      isEditable: isEditable,
      labourData: [],
      isEditLabour: false,
      editId: "",
    };
  }

  columns = [
    {
      name: "labourName",
      label: "Labour Name",
      options: {
        filter: true,
        sort: true,
      },
    },
    // {
    //   name: "mainActivity",
    //   label: "Main Activity",
    //   options: {
    //     filter: true,
    //     sort: true,
    //   },
    // },
    {
      name: "activity",
      label: "Activity",
      options: {
        filter: true,
        sort: true,
      },
    },
    {
      name: "unit",
      label: "Unit",
      options: {
        filter: true,
        sort: true,
      },
    },

    {
      name: "number_of_labourers",
      label: "Number of Labourers",
      options: {
        filter: true,
        sort: true,
      },
    },

    {
      name: "labourRate",
      label: "Salary",
      options: {
        filter: true,
        sort: false,
      },
    },
    {
      name: "labourAmount",
      label: "Amount",
      options: {
        filter: true,
        sort: false,
      },
    }, {
      name: "Edit",
      options: {
        customBodyRender: (value, tableMeta) => {
          return (
            <button
              onClick={() => {
                this.toggleEditLabour(tableMeta);
              }}
            >
              Edit
            </button>
          );
        },
      },
    },
    {
      name: "Delete",
      options: {
        customBodyRender: (value, tableMeta) => {
          return (
            <button onClick={() => this.deleteLabour(tableMeta.rowIndex)}>
              Delete
            </button>
          );
        },
      },
    },
  ];
  async getLabourData() {
    let projectId = await global.config.secureStorage.getItem("projectId");
    try {
      const res = await axios.post(
        `${global.config.backendURL}/labour/getLabour`,
        {
          projectId: projectId,
        }
      );
      this.setState({ labourData: res.data });
    } catch (err) {
      console.log(err);
    }
  }

  async componentDidMount() {
    await this.getLabourData();
    console.log("labour data");
    console.log(this.state.labourData);
  }

  handleChange = (event, value = null) => {
    if (value === null) {
      this.setState({
        [event.target.name]: event.target.value,
      });
    } else {
      this.setState({
        labourName: value,
      });
    }
  };
  handleActivity = event => {
    this.setState({ activity: event.target.value });
  };
  // handleMainActivity = event => {
  //   console.log(event)
  //   this.setState({ mainActivity: event.target.value});
  // };


  toggleEditLabour = tableMeta => {
    let singleLabour = [...this.state.labourData];
    singleLabour = singleLabour[tableMeta.rowIndex];
    console.log("rowIndex" + tableMeta.rowIndex);
    console.log(singleLabour);

    if (!this.state.isEditLabour) {
      this.setState({
        editId: singleLabour._id,
        labourName: singleLabour.labourName,
        activity: singleLabour.activity,
        unit: singleLabour.unit,
        number_of_labourers: singleLabour.number_of_labourers,
        labourRate: singleLabour.labourRate,
        labourAmount: singleLabour.labourRate * singleLabour.number_of_labourers,
        isEditLabour: !this.state.isEditLabour,
      });
    }
  };
  toggleCreateNewLabour = () => {
    this.setState({
      labourName: "",
      activity: "",
      unit: "",
      labourRate: "",
      labourAmount: "",
      number_of_labourers: "",
      isCreateNewLabour: !this.state.isCreateNewLabour,
    });
  };

  deleteLabour = async id => {
    let singleLabour = [...this.state.labourData];
    singleLabour = singleLabour[id];
    try {
      const res = await axios.post(
        `${global.config.backendURL}/labour/deleteLabour/${singleLabour._id}`
      );
      let tempData = this.state.labourData;
      tempData.splice(id, 1);
      console.log(tempData);
      this.setState({
        labourName: "",
        activity: "",
        unit: "",
        labourRate: "",
        labourAmount: "",
        number_of_labourers: "",
        labourData: tempData,
      });
    } catch (err) {
      console.log(err);
    }
  };

  updateLabour = async event => {
    event.preventDefault();

    let projectId = await global.config.secureStorage.getItem("projectId");

    const finalLabour = {
      projectId,
      labourName: this.state.labourName,
      activity: this.state.activity,
      unit: this.state.unit,
      number_of_labourers: this.state.number_of_labourers,
      labourRate: this.state.labourRate,
      labourAmount: this.state.labourRate * this.state.number_of_labourers,
      _id: this.state.editId,
    };

    const num = Number(this.state.labourName);
    if (Number.isInteger(num)) {
      alert("Please enter valid equipment");
      this.setState({ equipmentName: "" });
    } else {
      let tempData = [...this.state.labourData];
      console.log(this.state.editId);
      if (this.state.editId != undefined) {
        try {
          const res = await axios.post(
            `${global.config.backendURL}/labour/editLabour/${this.state.editId}`,
            {
              ...finalLabour,
            }
          );
        } catch (err) {
          console.log(err);
        }
        let objIndex = tempData.findIndex(obj => obj._id == this.state.editId);
        tempData[objIndex] = finalLabour;
      }
      this.setState({
        editId: "",
        isEditLabour: !this.state.isEditLabour,
        labourName: "",
        activity: "",
        unit: "",
        labourRate: "",
        labourAmount: "",
        number_of_labourers: "",
        labourData: tempData,
      });
    }
  };
  saveLabour = async event => {
    event.preventDefault();
    let projectId = await global.config.secureStorage.getItem("projectId");
    const finalLabour = {
      projectId,
      labourName: this.state.labourName,
      activity: this.state.activity,
      unit: this.state.unit,
      number_of_labourers: this.state.number_of_labourers,
      labourRate: this.state.labourRate,
      labourAmount: this.state.labourRate * this.state.number_of_labourers,
    };
    const num = Number(this.state.labourName);
    console.log(Number.isInteger(num));
    if (Number.isInteger(num)) {
      alert("Please enter valid labour,it cannot be a number");
      this.setState({ labourName: "" });
    } else {
      try {
        const res = await axios.post(
          `${global.config.backendURL}/labour/addToLabour`,
          {
            ...finalLabour,
          }
        );
      } catch (err) {
        console.log(err);
      }
      let tempData = [...this.state.labourData, finalLabour];
      console.log(tempData);
      this.setState({
        labourName: "",
        activity: "",
        unit: "",
        labourRate: "",
        labourAmount: "",
        number_of_labourers: "",
        isCreateNewLabour: !this.state.isCreateNewLabour,
        labourData: tempData,
      });
      window.location.reload();
    }
  };
  render() {
    const { isCreateNewLabour, isEditLabour } = this.state;
    if (isCreateNewLabour) {
      return (
        <AddLabour
          storeData={this.props.storeData}
          activity={this.state.activity}
          unit={this.state.unit}
          handleChange={this.handleChange}
          labourName={this.state.labourName}
          labourRate={this.state.labourRate}
          labourAmount={this.state.labourAmount}
          labourData={this.state.labourData}
          number_of_labourers={this.state.number_of_labourers}
          saveLabour={this.saveLabour}
          toggleCreateNewLabour={this.toggleCreateNewLabour}
          handleActivity={this.handleActivity}
        />
      );
    }

    if (isEditLabour) {
      return (
        <EditLabour
          storeData={this.props.storeData}
          activity={this.state.activity}
          unit={this.state.unit}
          handleChange={this.handleChange}
          labourName={this.state.labourName}
          labourRate={this.state.labourRate}
          labourAmount={this.state.labourAmount}
          labourData={this.state.labourData}
          number_of_labourers={this.state.number_of_labourers}
          updateLabour={this.updateLabour}
          toggleEditEquipment={this.toggleEditEquipment}
          handleActivity={this.handleActivity}
        />
      );
    }
    return (
      <div>
        {this.props.notifications && (
          <Notifications notifications={this.props.notifications} />
        )}
        {this.state.isEditable && (
          <section className={Style.button__display}>
            <Button
              onClick={this.toggleCreateNewLabour}
              startIcon={<Add />}
              size="large"
            >
              Add Labour
            </Button>
          </section>
        )}
        <Grid container justify="center">
          <Grid item>
            <MUIDataTable
              title={`Labour`}
              data={this.state.labourData}
              columns={this.columns}
              options={{
                selectableRows: "none",
                responsive: "simple",
                pagination: false,
                download: false,
                viewColumns: false,
                filter: true,
                print: false,
              }}
            />
          </Grid>
        </Grid>
      </div>
    );
  }
}

Display.propTypes = {
  labour: PropTypes.object.isRequired,
};

export default Display;
