import React, { Component } from "react";
import {
  Grid,
  Button,
  FormControl,
  Select,
  InputLabel,
  MenuItem,
  Typography,
} from "@material-ui/core";
import { connect } from "react-redux";
import BarchartData from "./Barchart";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import { Save } from "@material-ui/icons";
import Loading from "../../Components/Loading";

import { getWbs } from "../../Redux/wbsRedux/wbsAction";
class Wbs extends Component {
  constructor(props) {
    // console.log(iterList);
    super(props);
    this.state = {
      modalOpen: false,
      loading: true,
      wbsTotal: 0,
      wbsActivityNames: [],
      wbsActivityPer: [],
      sDate: "",
      eDate: "",
      accName: "",
    };
  }

  componentDidMount() {
    try {
    } catch (error) {
      //
    }
  }
  submit = async () => {
    console.log(this.state.accName, this.state.sDate, this.state.eDate);
    try {
      let activityName = this.state.accName;
      let sDate = this.state.sDate;
      let eDate = this.state.eDate;
      await this.props.getWbs(activityName, sDate, eDate);
      this.setState({
        wbsTotal: this.props.newWbs.total,
        wbsActivityNames: Object.keys(this.props.newWbs.taskperArr),
        wbsActivityPer: Object.values(this.props.newWbs.taskperArr),
        loading: this.props.loading,
      });
    } catch (error) {
      console.log(error);
    }
  };
  handleDropdownChange = e => {
    if (e.target.value != null) {
      this.setState({
        accName: e.target.value,
      });
    }
  };
  render() {
    // if (this.props.loading) {
    //   return <Loading loading={this.props.loading} />;
    // }
    if (this.state.wbsActivityNames.length != 0) {
      console.log(this.state.wbsActivityNames);
      console.log(this.state.wbsActivityPer);
    }
    return (
      <div className="container" style={{ marginTop: "20px" }}>
        <Grid container alignItems="center" justify="space-around">
          <div>
            <Typography>
              <strong>Select Activity :</strong> Not available
            </Typography>
            <FormControl fullWidth>
              <InputLabel id="demo-simple-select-label">Activity</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={this.state.accName}
                label="Age"
                onChange={this.handleDropdownChange}
              >
                <MenuItem value={"plumbing"}>Plumbing</MenuItem>
              </Select>
            </FormControl>
          </div>
          <div>
            <Typography>
              <strong>Start Date :</strong> Not available
            </Typography>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <KeyboardDatePicker
                // disabled={!props.isEditable}
                disableToolbar
                format="dd-MM-yyyy"
                margin="none"
                id="date-picker-inline"
                KeyboardButtonProps={{
                  "aria-label": "change date",
                }}
                value={this.state.sDate}
                placeholder="Not available"
                onChange={(date, value) => {
                  this.setState({
                    sDate: value,
                  });
                }}
              />
            </MuiPickersUtilsProvider>
          </div>
          <div>
            <Typography>
              <strong>End Date :</strong> Not available
            </Typography>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <KeyboardDatePicker
                // disabled={!props.isEditable}
                disableToolbar
                format="dd-MM-yyyy"
                margin="none"
                id="date-picker-inline"
                KeyboardButtonProps={{
                  "aria-label": "change date",
                }}
                value={this.state.eDate}
                placeholder="Not available"
                onChange={(date, value) => {
                  console.log(value);
                  this.setState({
                    eDate: value,
                  });
                }}
              />
            </MuiPickersUtilsProvider>
          </div>
        </Grid>
        <Grid
          container
          alignItems="center"
          justify="space-around"
          style={{ marginBottom: "40px" }}
        >
          <div>
            <Button onClick={this.submit} endIcon={<Save />}>
              Submit
            </Button>
          </div>
        </Grid>
        {this.state.wbsActivityNames.length != 0 ? (
          <BarchartData
            accNames={this.state.wbsActivityNames}
            percentage={this.state.wbsActivityPer}
          />
        ) : (
          ""
        )}
      </div>
    );
  }
}

const mapStateToProps = state => {
  // console.log(state.activityReducer);
  return {
    newWbs: state.wbsReducer.newWbs,
    loading: state.wbsReducer.loading,
    isError: state.wbsReducer.isError,
  };
};

export default connect(mapStateToProps, {
  getWbs,
})(Wbs);
