import React, { useState } from "react";
import { useTheme } from "@material-ui/core/styles";
import {
  Card,
  CardContent,
  Typography,
  Button,
  Grid,
  CardActions,
  Dialog,
  DialogActions,
  DialogTitle,
  DialogContent,
  DialogContentText,
  useMediaQuery,
  Toolbar,
  Select,
} from "@material-ui/core";
import { Info, Check, Close, Update } from "@material-ui/icons";
import FileBase64 from "react-file-base64";
const DetailsDialog = props => {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("xl"));
  var checklist = props.checklist;
  const [approveImage, setApproveImage] = useState("");

  return (
    <div>
      <Dialog
        open={props.open}
        fullScreen={fullScreen}
        aria-labelledby="form-dialog-title"
      >
        <DialogContent>
          <Toolbar />
          <Grid container justify="center">
            <Grid item justify="center">
              <img
                justify="center"
                style={{ width: "100%" }}
                src={checklist.checkListImage}
              />
              <Typography variant="h5">
                {props.checklist.checklistTitle}
              </Typography>
              <DialogContentText>Checklist Details</DialogContentText>
              <Typography>
                <strong>Checklist ID:</strong> {checklist.checklistId}
              </Typography>
              <Typography>
                <strong>Project Name :</strong> {checklist.projectName}
              </Typography>
              <Typography></Typography>
              <Typography>
                <strong>Date Created:</strong>{" "}
                {new Date(checklist.createdDate).toDateString() ||
                  "Not available"}
              </Typography>
              <Typography>
                <strong>Image:</strong>
              </Typography>
              <hr />
              <Typography>
                <strong>Submitted By:</strong> {checklist.submittedBy}
              </Typography>
              <Typography>
                <strong>To Be Checked By :</strong> {checklist.checkedBy}
              </Typography>
              <Typography>
                <strong>To Be Approved By:</strong> {checklist.approvedBy}
              </Typography>
              <Typography>
                <strong>Approved Image:</strong>
                <img
                  justify="center"
                  style={{ width: "100%" }}
                  src={checklist.approveImage}
                />{" "}
              </Typography>
              <hr />
              <Typography
                variant="h6"
                style={{ fontWeight: 700, marginBottom: "0.5em" }}
              >
                Checklist points:
              </Typography>
              {Object.keys(checklist.subChecklist).map((item, index) => {
                var point = checklist.subChecklist[item];
                var num = index + 1;
                return (
                  <Typography key={index}>
                    <strong>{num + ". " + point.description}:</strong>{" "}
                    {point.type || "Not available"}
                  </Typography>
                );
              })}
              <hr />{" "}
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions style={{ marginBottom: "1.5em", marginRight: "1em" }}>
          <Button
            color="secondary"
            variant="contained"
            size="large"
            startIcon={<Close />}
            onClick={props.toggleDialog}
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

const ChecklistCard = props => {
  //
  var checklist = props.item;
  const [details, setDetails] = useState(false);
  return (
    <Grid item xs={12} sm={3}>
      <DetailsDialog
        open={details}
        toggleDialog={() => setDetails(prev => !prev)}
        checklist={checklist}
      />
      <Card variant="outlined" style={{ padding: "5px" }} elevation={3}>
        <CardContent>
          <Typography variant="h5" style={{ marginBottom: "0.5em" }}>
            <strong>{checklist.checklistTitle}</strong>
          </Typography>
          <Typography>
            <strong>Checklist ID:</strong> {checklist.checklistId}
          </Typography>
          <Typography>
            <strong>Date of Creation :</strong>{" "}
            {new Date(checklist.createdDate).toDateString() || "Not available"}
          </Typography>
          <Typography>
            <strong>Project Name:</strong> {checklist.projectName}
          </Typography>
          <Typography>
            <strong>Location:</strong> {checklist.location}
          </Typography>
          <Grid container spacing={1} style={{ marginTop: "1em" }}>
            <Grid item>
              <Button
                variant="contained"
                color="primary"
                onClick={() => setDetails(prev => !prev)}
                startIcon={<Info />}
              >
                View Details
              </Button>
            </Grid>
            <Grid item>
              {props.isCheckVisible && props.checkable && (
                <Button
                  disabled={!props.checkable}
                  startIcon={<Check />}
                  onClick={e => {
                    e.preventDefault();

                    props.approveCheckList(
                      checklist._id,
                      global.config.secureStorage.getItem("subprojectId"),
                      props.subActivity,
                      props.item.type,
                      false
                    );
                  }}
                >
                  Send for approval
                </Button>
              )}
            </Grid>
            <Grid item>
              {props.isApproveVisible && !props.checkable && props.approvable && (
                <>
                  <Button
                    color="primary"
                    style={{ margin: "10px 20px 0 0", width: "200px" }}
                    startIcon={<Update />}
                  >
                    <FileBase64
                      multiple={false}
                      className="btn"
                      value={props.approveImage}
                      onDone={file => {
                        props.handelApproveImg(file.base64);
                        // files.forEach(ele => ele.base64);
                        // props.handlePanaroma(file.base64);
                      }}
                    />
                  </Button>
                  <Button
                    disabled={!props.approvable}
                    startIcon={<Check />}
                    onClick={e => {
                      e.preventDefault();

                      if (props.approveImage) {
                        props.approveCheckList(
                          checklist._id,
                          global.config.secureStorage.getItem("subprojectId"),
                          props.subActivity,
                          props.approveImage,
                          props.item.type,
                          true
                        );
                      } else {
                        alert("Select An Image before Sending For Approval");
                      }
                    }}
                  >
                    {props.approvable ? "Approve" : "Approved"}
                  </Button>
                </>
              )}
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Grid>
  );
};

export default ChecklistCard;
