import React from "react";
import { useTheme } from "@material-ui/core/styles";
import {
  makeStyles,
  Typography,
  Button,
  Grid,
  Card,
  CardContent,
} from "@material-ui/core";

const useStyles = makeStyles({
  btn: {
    padding: 20,
    backgroundColor: "#333",
    color: "#fff",
  },
  title: {
    backgroundColor: "#345BAE",
    padding: "11px 10px",
    color: "#fff",
    borderRadius: 11,
  },
  footer: {
    width: "100%",
    textAlign: "right",
  },
  footerlink: {
    backgroundColor: "#345BAE",
    padding: "11px 10px",
    borderRadius: 11,
    color: "#fff",
  },
  getcol: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-around",
    width: "100%",
  },
});
function ViewUpdates(props) {
  //   console.log(props.location.state.activityDetails);
  let updateDataSorted = props.location.state.activityDetails;
  const classes = useStyles();

  return (
    <>
      {updateDataSorted
        ? updateDataSorted.map((detail, detailIndex) => {
            return (
              <Card
                variant="outlined"
                style={{ margin: "5px", width: "100%" }}
                elevation={3}
              >
                <CardContent>
                  <Grid container spacing={2}>
                    <Grid item xs={12} md={12}>
                      <Typography variant="p" className={classes.getcol}>
                        Updated by:{detail.data[detail.data.length - 1].user}
                        {/* {detail.user} */}
                      </Typography>
                      <Typography variant="p" className={classes.getcol}>
                        Update Date :{detail.data[detail.data.length - 1].date}
                      </Typography>
                      <Typography variant="p" className={classes.getcol}>
                        Activity Name:
                        {detail.data[detail.data.length - 1].activityName}
                      </Typography>
                      <Typography variant="p" className={classes.getcol}>
                        Update Percentage:
                        {detail.data[detail.data.length - 1].updatedPercentage}%
                      </Typography>
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            );
          })
        : "No Updates"}
    </>
  );
}

export default ViewUpdates;
