import { SUCCESS_GET_WBS, ERROR_GET_WBS, LOADING_WBS } from "./wbsTypes";

const initialState = {
  loading: true,
  newWbs: [],
  isError: false,
};
const misReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOADING_WBS:
      return {
        ...state,
        loading: true,
      };
    case ERROR_GET_WBS:
      return {
        ...state,
        loading: false,
        isError: true,
      };
    case SUCCESS_GET_WBS:
      return {
        ...state,
        newWbs: action.payload,
        loading: false,
      };
    default:
      return {
        ...state,
      };
  }
};

export default misReducer;
